/* ================= General Grid Containers ================= */

/* For Bot Army */
.army-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 1.5rem;
    padding: 1rem;
  }
  
  /* For Bot Collection */
  .bot-collection {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 1.5rem;
    padding: 1rem;
  }
  
  /* ================= Bot Card Styles ================= */
  
  .bot-card {
    background: #252830;
    border: 1px solid var(--border);
    border-radius: 12px;
    overflow: hidden;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    max-width: 250px;
    transition: transform 0.3s, box-shadow 0.3s, border 0.3s;
    cursor: pointer;
    position: relative;
  }
  
  .bot-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(163, 113, 247, 0.25);
    border: 1px solid var(--primary);
  }
  
  .bot-card.enlisted {
    border: 2px solid #3fb950;
  }
  
  .bot-card img {
    width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 1rem;
  }
  
  .bot-card h3 {
    font-size: 1.2rem;
    color: var(--text-primary);
    margin-bottom: 0.5rem;
  }
  
  .bot-card p {
    font-size: 0.9rem;
    color: var(--text-secondary);
    margin-bottom: 0.5rem;
  }
  
  /* ================= Bot Card Badges ================= */
  
  .level-badge {
    background-color: var(--primary);
    color: white;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 1.2rem;
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
  }
  
  /* ================= Bot Card Buttons ================= */
  
  .discharge-btn {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    background: #f85149;
    color: white;
    border: none;
    border-radius: 6px;
    padding: 0.3rem 0.6rem;
    font-size: 0.8rem;
    cursor: pointer;
    transition: background 0.2s;
  }
  
  .discharge-btn:hover {
    background: #da3633;
  }
  
  /* ================= Army Container & Empty State ================= */
  
  .army-container {
    background: rgba(30, 35, 40, 0.8);
    border-radius: 12px;
    padding: 1rem;
    margin-bottom: 2rem;
    border: 1px solid #444;
  }
  
  .army-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .army-stats {
    display: flex;
    gap: 1rem;
    font-size: 0.9rem;
    color: #aaa;
  }
  
  .empty-army {
    text-align: center;
    padding: 2rem;
    color: #888;
    font-size: 1rem;
  }
  